body {
  margin: 0 auto;
  height: 100vh;
  overflow: hidden;

}

.App {
  height: 100vh;
  width: 100%;
  display: block;
  overflow: hidden;
  -webkit-user-select: none;
  -webkit-touch-callout: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.dragable {
  position: absolute;
  transition: ease-in-out .1s;
}

.window {
  background: red;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content auto;
  grid-template-areas:
    "dragarea"
    "content";
  top: 20%;
  left: 20%;
  width: 200px;
}

.window .drag-area {
  grid-area: dragarea;
  background: blue;
  display:flex;
}

.content {
  grid-area: content;
  background: yellow;
}

.togglefullscreen {
  display: block;
  width: 25px;
  height: 25px;
  margin: 5px;
  background: green;
}

.fullscreen {
  top: 0 !important;
  left: 0 !important;
  width: 100%;
  height: 100%;

}

.close {
  transform: scale(0);
}

.icon {
  height: 50px;
  width: 35px;

  position: fixed;
}

.icon .drag-area {
  width: 100%;
  height: 100%;
  background: orange;
}
.closeButton{
  display: block;
  width: 25px;
  height: 25px;
  margin: 5px;
  background: red;
}